import { useState, useEffect } from 'react';
import GameSetup from './components/GameSetup';
import InGame from './components/InGame';
import './styles.css';
import bg from './assets/bg-lite.jpg';

function App() {
	const initialState = () => JSON.parse(window.localStorage.getItem('mtg-player-data')) || [];
	const [data, setData] = useState(initialState);
	const [newPlayers, setNewPlayers] = useState([]);
	const [game, setGame] = useState(false);

	useEffect(() => {
		window.localStorage.setItem('mtg-player-data', JSON.stringify(data));
	}, [data]);

	const imageStyling = {
		position: 'absolute',
		left: -190 + 'px',
		height: window.outerHeight,
		zIndex: -1,
	}

	return (
		<div className="App">
			<img src={bg} alt="background" style={imageStyling}></img>
			{ game === false ?
				<GameSetup 
					newPlayers={newPlayers}
					setNewPlayers={setNewPlayers}
					setGame={setGame}
					setData={setData}
				/>
			:
				<InGame 
					newPlayers={newPlayers}
					setNewPlayers={setNewPlayers}
					setData={setData}
					setGame={setGame}
				/>
			}
		</div>
	);
};

export default App;